    pushState (query) {
      // let _this = this
      let popStateData = {}

      popStateData.userInfo = this.userInfo
      popStateData.favoriteExpertList = this.favoriteExpertList
      popStateData.expertQueryList = this.expertQueryList
      popStateData.queryCondition = this.queryCondition
