    // -
    previousPaperPage () {
      'use strict'
      let _this = this

      let jqCurrentPage = $('#current-page')
      let jqPaperPreviousPage = $('#paper-previous-page')
      let jqPaperNextPage = $('#paper-next-page')

      if (!this.paperPageCurrent || this.paperPageCurrent <= 0 || this.paperPageCurrent > this.paperPageTotal) {
        console.log('pre-page-error! paperPageCurrent is out of range.')
        return false
      } else if (this.paperPageCurrent === 1) {
        alert('')
        return false
      } else {
        this.paperPageCurrent--

        jqCurrentPage.empty().append(this.paperPageCurrent)

        // ,
        if (this.paperPageCurrent === 1 && !jqPaperPreviousPage.hasClass('disabled')) {
          jqPaperPreviousPage.addClass('disabled')
        }

        // disabled,
        if (jqPaperNextPage.hasClass('disabled')) {
          jqPaperNextPage.removeClass('disabled')
        }

        // 
        _this.changePaperPage()
      }
      return true
    },
    // -
    nextPaperPage () {
      'use strict'
      let _this = this

      let jqCurrentPage = $('#current-page')
      let jqPaperPreviousPage = $('#paper-previous-page')
      let jqPaperNextPage = $('#paper-next-page')

      if (!this.paperPageCurrent || this.paperPageCurrent <= 0 || this.paperPageCurrent > this.paperPageTotal) {
        console.log('next-page-error! paperPageCurrent is out of range.')
        return false
      } else if (this.paperPageCurrent === this.paperPageTotal) {
        alert('')
        return false
      } else {
        this.paperPageCurrent++

        jqCurrentPage.empty().append(this.paperPageCurrent)

        // ,
        if (this.paperPageCurrent === this.paperPageTotal && !jqPaperNextPage.hasClass('disabled')) {
          jqPaperNextPage.addClass('disabled')
        }

        // disabled,
        if (jqPaperPreviousPage.hasClass('disabled')) {
          jqPaperPreviousPage.removeClass('disabled')
        }

        // 
        _this.changePaperPage()
      }
      return true
    },
    // -
    jumpPaperPage () {
      'use strict'
      let _this = this

      let jqCurrentPage = $('#current-page')
      let jqPaperPreviousPage = $('#paper-previous-page')
      let jqPaperNextPage = $('#paper-next-page')
      let jqPaperJumpInput = $('#paper-jump-input')
      let pageInput = Math.floor(parseInt(jqPaperJumpInput.val().toString().trim(), 10))

      if (!this.paperPageCurrent || this.paperPageCurrent <= 0 || this.paperPageCurrent > this.paperPageTotal) {
        console.log('input-jump-page-error! paperPageCurrent is out of range.')
        return false
      } else if (!pageInput || pageInput === '') {
        alert('')
        jqPaperJumpInput.focus()
        return false
      } else if (parseInt(pageInput, 10) <= 0 || parseInt(pageInput, 10) > this.paperPageTotal) {
        alert('' + this.paperPageTotal + '')
        jqPaperJumpInput.focus()
        return false
      } else {
        this.paperPageCurrent = parseInt(pageInput, 10)

        jqCurrentPage.empty().append(this.paperPageCurrent)

        // ,
        if (this.paperPageCurrent === 1 && !jqPaperPreviousPage.hasClass('disabled')) {
          jqPaperPreviousPage.addClass('disabled')
        }

        // ,
        if (this.paperPageCurrent === this.paperPageTotal && !jqPaperNextPage.hasClass('disabled')) {
          jqPaperNextPage.addClass('disabled')
        }

        // ,
        if (this.paperPageCurrent > 1 && this.paperPageCurrent < this.paperPageTotal) {
          if (jqPaperPreviousPage.hasClass('disabled')) {
            jqPaperPreviousPage.removeClass('disabled')
          }
          if (jqPaperNextPage.hasClass('disabled')) {
            jqPaperNextPage.removeClass('disabled')
          }
        }

        // 
        _this.changePaperPage()
      }

      return true
    },
    // 
    changePaperPage () {
      'use strict'
      let _this = this

      if (!this.detailPageData || this.detailPageData.length === 0 || this.detailPageData === '' ||
        this.paperPageCurrent <= 0 || this.paperPageTotal <= 0 ||
        this.paperPageCurrent > this.paperPageTotal) {
        console.log('function changePaperPage() error: global variables error.')
        return false
      }

      let expertId = this.expertIdCurrent
      let currentPage = this.paperPageCurrent
      let startDate = new Date()
      let endDate = startDate

      let serverUrl = '/paper_list'

      let postData = {}
      if (!expertId || expertId === '') {
        return false
      } else {
        postData.id = expertId
        postData.current_page = currentPage
      }

      // HTTP GET (JQuery Ajax)
      $.ajax({
        type: 'GET',
        url: serverUrl,
        data: postData,
        dataType: 'json',
        beforeSend: function (xhr) {
          endDate = new Date()
          console.log('', endDate - startDate)
        },
        success: function (result, status, xhr) {
          endDate = new Date()
          console.log(' + AJAX', endDate - startDate)

          console.log('$.ajax success, state = ' + xhr.status)

          if (!result || result === '' || result.length === 0) {
            alert('')
            console.log('')
            return false
          }

          //  JSON 
          _this.detailPageData.papers = JSON.parse(result.papers)

          console.log(_this.detailPageData)
          console.log(JSON.parse(_this.detailPageData.papers[0]))

          // 
          _this.pushState('expert_detail/' + expertId)

          //  TODO 
          let jqThisResearcherPapers = $('#researcher-papers ul.papers')
          let jqPaperJumpInput = $('#paper-jump-input')

          let paperItem
          let htmlTemplate = ''
          let thisPagePapers = _this.detailPageData.papers
          thisPagePapers.forEach(function (item, index) {
            // JSON
            paperItem = JSON.parse(item)
            // console.log(paperItem)

            // authors
            let paperAuthorsName = paperItem.authors.split(' ').slice(0, 5)

            // id(id,)
            let paperAuthorsId = []
            if (paperItem.author1 && (paperItem.author1 !== '')) {
              paperAuthorsId.push(paperItem.author1)
            } else {
              paperAuthorsId.push('')
            }
            if (paperItem.author2 && (paperItem.author2 !== '')) {
              paperAuthorsId.push(paperItem.author2)
            } else {
              paperAuthorsId.push('')
            }
            if (paperItem.author3 && (paperItem.author3 !== '')) {
              paperAuthorsId.push(paperItem.author3)
            } else {
              paperAuthorsId.push('')
            }
            if (paperItem.author4 && (paperItem.author4 !== '')) {
              paperAuthorsId.push(paperItem.author4)
            } else {
              paperAuthorsId.push('')
            }
            if (paperItem.author5 && (paperItem.author5 !== '')) {
              paperAuthorsId.push(paperItem.author5)
            } else {
              paperAuthorsId.push('')
            }

            // html
            htmlTemplate += '<li><article><header><h3><a>' + paperItem.title + '</a></h3></header>'

            // 
            htmlTemplate += '<p>'
            let i
            for (i = 0; i < paperAuthorsName.length; i++) {
              if (paperAuthorsId[i] === '') {
                // id,,
                htmlTemplate += (paperAuthorsName[i] || paperAuthorsId[i]) + ' , '
              } else {
                htmlTemplate += '<a class="paper-author-item" data-id="' + paperAuthorsId[i] + '">' +
                  (paperAuthorsName[i] || paperAuthorsId[i]) + '</a>' + ' , '
              }
            }
            // ' , '
            htmlTemplate = htmlTemplate.substring(0, (htmlTemplate.length - 3))

            // 
            htmlTemplate += ' | ' + (paperItem.type || '') + ' | ' + paperItem.source + '</p>'

            htmlTemplate += '<p><strong>' + '</strong>: ' + paperItem.abstract + '</p>'

            htmlTemplate += '<p><strong>' + '</strong>: ' + paperItem.keyword + '</p>'

            htmlTemplate += '</article></li>'
          })
          // console.log(htmlTemplate)
          jqThisResearcherPapers.empty()
            .append(htmlTemplate)

          // 
          jqPaperJumpInput.val('')

          // 
          $('#researcher-papers ul.papers a.paper-author-item').unbind('click').click(function () {
            _this.jumpToAnotherResearcher($(this).attr('data-id'))
          })

          $('#researcher-papers ul.papers a.jump-paper-detail').unbind('click').click(function () {
            _this.jumpPaperDetailPage($(this).attr('data-id'))
          })

          endDate = new Date()
          console.log(' + AJAX + ', endDate - startDate)

          return true
        },
        error: function (xhr, status, error) {
          console.log('$.ajax error, state = ' + xhr.status)
          alert('HTTP GET Error! status=' + status + '&error=' + error +
            '&statusCode=' + xhr.status + '&responseText=' + xhr.responseText +
            '&readyState=' + xhr.readyState)
        },
        statusCode: {
          // 
          200: function () {
            console.log('200: ')
          },
          404: function () {
            console.log('404: ')
            alert('404: ')
          },
          500: function () {
            console.log('500: ')
            alert('500: ')
          }
        }
      })
    },
