  }

  /* Header */

  body {
    padding-top: 3.5em;
  }

  header.edms-header {
    display: -moz-flex;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    -moz-justify-content: space-between;
    -webkit-justify-content: space-between;
    -ms-justify-content: space-between;
    justify-content: space-between;
    background-color: #ffffff;
    border-bottom: solid 1px rgba(160, 160, 160, 0.3);
    height: 3.5em;
    left: 0;
    line-height: 3.5em;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 10000;
  }

  header.edms-header a {
    color: inherit;
    text-decoration: none;
  }

  header.edms-header ul {
    list-style: none;
