    -moz-flex: 1;
    -webkit-flex: 1;
    -ms-flex: 1;
    flex: 1;
    border-left: solid 1px rgba(160, 160, 160, 0.3);
    height: inherit;
    line-height: inherit;
    margin-left: 1.5em;
    overflow: hidden;
    padding-left: 1.5em;
  }

  header.edms-header .links ul {
    height: inherit;
    line-height: inherit;
    list-style: none;
  }

  header.edms-header .links ul li {
    display: inline-block;
    border-left: solid 1px rgba(160, 160, 160, 0.3);
    /*line-height: 1;*/
    line-height: inherit;
    margin-left: 1em;
    padding-left: 1em;
    cursor: pointer;
    vertical-align: middle;
  }

  header.edms-header .links ul li:first-child {
    border-left: 0;
    margin-left: 0;
    padding-left: 0;
  }

  header.edms-header .links ul li:last-child {
