  header.edms-header .links ul li a {
    border-bottom: 0;
    font-family: "Raleway", Helvetica, sans-serif;
    font-size: 0.7em;
    font-weight: 400;
    letter-spacing: 0.25em;
    text-transform: uppercase;
    vertical-align: middle;
  }

  header.edms-header .main {
    height: inherit;
    line-height: inherit;
    text-align: right;
  }

  header.edms-header .main ul {
    height: inherit;
    line-height: inherit;
    list-style: none;
  }

  header.edms-header .main ul li {
    display: inline-block;
    border-left: solid 1px rgba(160, 160, 160, 0.3);
    height: inherit;
    line-height: inherit;
    white-space: nowrap;
    vertical-align: middle;
  }

  header.edms-header .main ul li > * {
    display: block;
    float: left;
  }

  header.edms-header .main ul li > a {
    text-decoration: none;
    cursor: pointer;
    border-bottom: 0;
    color: #aaaaaa;
    overflow: hidden;
    position: relative;
    text-indent: 4em;
    width: 4em;
    vertical-align: middle;
  }

  header.edms-header .main ul li > a:before {
    -moz-osx-font-smoothing: grayscale;
