  /* load container keyframes */

  @keyframes loading {
    0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
      box-shadow: -0.11em -0.83em 0 -0.4em #ee2c2c,
      -0.11em -0.83em 0 -0.42em #6495ed,
