      text-align: center;
      text-decoration: none;
      font-family: "Raleway", Helvetica, sans-serif;
    }

    #intro header p {
      font-size: 0.7em;
    }

  }

  @media screen and (max-width: 736px) {

    #intro {
      margin: 0 0 1.5em 0;
      padding: 1.25em 0;
    }

    #intro > :last-child {
      margin-bottom: 0;
    }

    #intro .logo {
      margin: 0 0 0.5em 0;
    }

    #intro header h2.this-researcher-name {
      font-size: 1.25em;
      font-weight: 900;
      text-align: center;
      text-decoration: none;
      font-family: "Raleway", Helvetica, sans-serif;
    }

    #intro header > :last-child {
      margin-bottom: 0;
    }

  }

  /* Footer */

  #footer .icons {
    color: #aaaaaa;
  }

  #footer .copyright {
    color: #aaaaaa;
    font-family: "Raleway", Helvetica, sans-serif;
    font-size: 0.5em;
    font-weight: 400;
    letter-spacing: 0.25em;
    text-transform: uppercase;
  }

  body.single #footer {
    text-align: center;
  }

  div#detail div.relationship-floating-div {
    display: block;
    height: fit-content;
    width: fit-content;
    max-width: 20em;
    max-height: 50em;
    background-color: #fafafa;
    overflow: scroll;
    border: 1px solid transparent;
  }

  div#detail div.relationship-floating-div p {
    margin: 0;
    padding: 0;
    color: #4f94cd;
  }

  div.opinion-div h3 {
    margin: 0 0 0.2em 0;
    padding: 0;
    font-size: 1em;
  }

  div.opinion-div p {
    margin: 0;
    padding: 0;
    font-size: 0.8em;
  }
</style>



