    // 
    pushState (query) {
      let _this = this
      let popStateData = {}

      popStateData.indexPageData = _this.indexPageData // 
      popStateData.expertQueryList = _this.expertQueryList // 
      popStateData.queryCondition = _this.queryCondition // 
      popStateData.expertQueryListPageTotal = _this.expertQueryListPageTotal // -
      popStateData.expertQueryListPageCurrent = _this.expertQueryListPageCurrent // -
      popStateData.activeHottestExpert = _this.activeHottestExpert // 
      popStateData.showMenu = _this.showMenu // 
      console.log(popStateData)

      if (location.href.indexOf('#') !== -1) {
        console.log(': ' + location.href.split('#')[0] + '#/' + query)
        history.pushState(popStateData, document.title, location.href.split('#')[0] + '#/' + query)
      } else {
        console.log(': ' + location.href)
        history.pushState(popStateData, document.title, location.href)
      }
    },
