    // AJAX 
    loadHomePage () {
      'use strict'
      let _this = this

      let startDate = new Date()
      let endDate

      let serverUrl = '/'

      // HTTP GET (JQuery Ajax)
      $.ajax({
        type: 'GET',
        url: serverUrl,
        dataType: 'json',
        // async: true, // 
        // timeout: 10000, // 10
        beforeSend: function (xhr) {
          endDate = new Date()
          console.log(endDate - startDate)

          // console.log('$.ajax beforeSend, state = ' + xhr.status)
          // loading
          $('#intro .load-container').css({'display': 'block'})
        },
        success: function (result, status, xhr) {
          endDate = new Date()
          console.log(endDate - startDate)

          // console.log('$.ajax success, state = ' + xhr.status)
          // loading
          $('#intro .load-container').css({'display': 'none'})

          if (!result || result === '' || result.length === 0) {
            console.log('')
            return false
          }

          // 
          // _this.ajaxSuccess = true
          _this.indexPageData = {}

          // JSON
          _this.indexPageData.ccn_experts = JSON.parse(result.ccn_experts)
          _this.indexPageData.hottest_five_organizations = []
          _this.indexPageData.hottest_five_experts = []

          let indexPageDataTemp = _this.indexPageData

          let ccnExpertsTemp = JSON.parse(result.ccn_experts)
          ccnExpertsTemp.forEach(function (item, index) {
            let jsonTemp = []
            item.experts.forEach(function (_item, _index) {
              jsonTemp.push(JSON.parse(_item))
            })
            indexPageDataTemp.ccn_experts[index].experts = jsonTemp
          })

          let hottestFiveOrganizationsTemp = JSON.parse(result.hottest_five_organizations)
          hottestFiveOrganizationsTemp.forEach(function (item, index) {
            indexPageDataTemp.hottest_five_organizations.push(JSON.parse(item))
          })

          let hottestFiveExpertsTemp = JSON.parse(result.hottest_five_experts)
          hottestFiveExpertsTemp.forEach(function (item, index) {
            indexPageDataTemp.hottest_five_experts.push(JSON.parse(item))
          })
          _this.indexPageData = indexPageDataTemp

          // console.log(_this.indexPageData)
          // console.log(_this.indexPageData.hottest_five_organizations)
          // console.log(_this.indexPageData.hottest_five_experts)

          //  AJAX 
          _this.getHomeAjaxData(_this)

          // 
          _this.replaceState('')

          // 
          _this.renderIndexPage(_this.indexPageData)

          endDate = new Date()
          console.log(endDate - startDate)

          return true
        },
        error: function (xhr, status, error) {
          console.log('$.ajax error, state = ' + xhr.status)
          // loading
          $('#intro .load-container').css({'display': 'none'})
          alert('HTTP GET Error! status=' + status + '&error=' + error +
            '&statusCode=' + xhr.status + '&responseText=' + xhr.responseText +
            '&readyState=' + xhr.readyState)
        },
        statusCode: {
          // 
          200: function () {
            console.log('200: ')
          },
          404: function () {
            console.log('404: ')
            alert('404: ')
          },
          500: function () {
            console.log('500: ')
            alert('500: ')
          }
        }
      })
    },
