    // 
    renderIndexPage (result) {
      'use strict'
      let _this = this

      let ccnExperts = result.ccn_experts
      let hottestFiveExperts = result.hottest_five_experts
      let hottestFiveOrganizations = result.hottest_five_organizations

      if (!ccnExperts || ccnExperts.length === 0 || ccnExperts === '' ||
        !hottestFiveExperts || hottestFiveExperts.length === 0 || hottestFiveExperts === '' ||
        !hottestFiveOrganizations || hottestFiveOrganizations.length === 0 || hottestFiveOrganizations === '') {
        console.log('function renderIndexPage() error: global variables error.')
        return false
      }

      //  & 
      _this.renderExperts('hottest-five-experts', result.hottest_five_experts)
      _this.renderOrganizations('hottest-five-organizations', result.hottest_five_organizations)

      //  A, F, K, R, U ,  result.ccn_experts  0, 5, 10, 15, 18
      let firstShowFieldList = [0, 5, 10, 15, 18]

      // 
      for (let i = 0; i < firstShowFieldList.length; i++) {
        _this.renderExperts('ccn-hottest-expert-' + (i + 1).toString(),
          ccnExperts[firstShowFieldList[i]].experts)
      }
