    //  CCN 
    ccnToList (cnnNumber) {
      let result
      if (cnnNumber === 'A' || cnnNumber === 'a' || cnnNumber === 'ccn-a') {
        result = 0
      } else if (cnnNumber === 'B' || cnnNumber === 'b' || cnnNumber === 'ccn-b') {
        result = 1
      } else if (cnnNumber === 'C' || cnnNumber === 'c' || cnnNumber === 'ccn-c') {
        result = 2
      } else if (cnnNumber === 'D' || cnnNumber === 'd' || cnnNumber === 'ccn-d') {
        result = 3
      } else if (cnnNumber === 'E' || cnnNumber === 'e' || cnnNumber === 'ccn-e') {
        result = 4
      } else if (cnnNumber === 'F' || cnnNumber === 'f' || cnnNumber === 'ccn-f') {
        result = 5
      } else if (cnnNumber === 'G' || cnnNumber === 'g' || cnnNumber === 'ccn-g') {
        result = 6
      } else if (cnnNumber === 'H' || cnnNumber === 'h' || cnnNumber === 'ccn-h') {
        result = 7
      } else if (cnnNumber === 'I' || cnnNumber === 'i' || cnnNumber === 'ccn-i') {
        result = 8
      } else if (cnnNumber === 'J' || cnnNumber === 'j' || cnnNumber === 'ccn-j') {
        result = 9
      } else if (cnnNumber === 'K' || cnnNumber === 'k' || cnnNumber === 'ccn-k') {
        result = 10
      } else if (cnnNumber === 'N' || cnnNumber === 'n' || cnnNumber === 'ccn-n') {
        result = 11
      } else if (cnnNumber === 'O' || cnnNumber === 'o' || cnnNumber === 'ccn-o') {
        result = 12
      } else if (cnnNumber === 'P' || cnnNumber === 'p' || cnnNumber === 'ccn-p') {
        result = 13
      } else if (cnnNumber === 'Q' || cnnNumber === 'q' || cnnNumber === 'ccn-q') {
        result = 14
      } else if (cnnNumber === 'R' || cnnNumber === 'r' || cnnNumber === 'ccn-r') {
        result = 15
      } else if (cnnNumber === 'S' || cnnNumber === 's' || cnnNumber === 'ccn-s') {
        result = 16
      } else if (cnnNumber === 'T' || cnnNumber === 't' || cnnNumber === 'ccn-t') {
        result = 17
      } else if (cnnNumber === 'U' || cnnNumber === 'u' || cnnNumber === 'ccn-u') {
        result = 18
      } else if (cnnNumber === 'V' || cnnNumber === 'v' || cnnNumber === 'ccn-v') {
        result = 19
      } else if (cnnNumber === 'X' || cnnNumber === 'x' || cnnNumber === 'ccn-x') {
        result = 20
      } else if (cnnNumber === 'Z' || cnnNumber === 'z' || cnnNumber === 'ccn-z') {
        result = 21
      } else {
        result = 0
      }
      return result
    },
    //  div 
    listToDiv (listNumber) {
      let result
      if (listNumber === 0 || listNumber === 1 || listNumber === 2 || listNumber === 3 || listNumber === 4) {
        result = 1
      } else if (listNumber === 5 || listNumber === 6 || listNumber === 7 || listNumber === 8 || listNumber === 9) {
        result = 2
      } else if (listNumber === 10 || listNumber === 11 || listNumber === 12 ||
        listNumber === 13 || listNumber === 14) {
        result = 3
      } else if (listNumber === 15 || listNumber === 16 || listNumber === 17) {
        result = 4
      } else if (listNumber === 18 || listNumber === 19 || listNumber === 20 || listNumber === 21) {
        result = 5
      } else {
        result = 1
      }
      return result
    },
    // AJAX 
    queryListAjax (postData, currentPage) {
      'use strict'
      let _this = this
      let serverUrl = 'expert_list'

      if (postData === null || (typeof postData) === 'undefined' || postData === {} ||
        currentPage === null || (typeof currentPage) === 'undefined' || currentPage === '') {
        console.log('queryListAjax error ')
        return false
      } else {
        if (postData.query_type !== 'normal' && postData.query_type !== 'advanced') {
          // loading
          $('div.load-container').css({'display': 'none'})
        } else {
          postData.current_page = currentPage

          // console.log(postData)
          // alert(postData)

          // HTTP GET (JQuery Ajax)
          $.ajax({
            type: 'GET',
            url: serverUrl,
            data: postData,
            dataType: 'json',
            beforeSend: function (xhr) {
              // loading
              $('div.load-container').css({'display': 'block'})
            },
            success: function (result, status, xhr) {
              // alert('HTTP GET Success! normal_query: ' + 'result=' + result);
              // 
              _this.expertQueryList = []
              _this.expertQueryListPageTotal = 0
              _this.expertQueryListPageCurrent = currentPage

              // 
              let itemPerPage = 10

              if (!result || result === '' || result.length === 0) {
                // 
                let htmlTemplate = '<p id="search-result-tag"></p>'
                //  query-result-list 
                //  query-result-list 
                $('.query-result-list').empty().append(htmlTemplate)
                return false
              }

              _this.expertQueryList = JSON.parse('[' + result + ']')

              let totalPage = _this.expertQueryList[_this.expertQueryList.length - 1]
              _this.expertQueryListPageTotal = Math.floor(totalPage / itemPerPage) + 1

              result = _this.expertQueryList

              console.log(result)

              //  AJAX 
              _this.getListAjaxData(_this)

              // 
              _this.renderQueryList()
            },
            error: function (xhr, status, error) {
              // loading
              $('div.load-container').css({'display': 'none'})
              alert('HTTP GET Error! status=' + status + '&error=' + error +
                '&statusCode=' + xhr.status + '&responseText=' + xhr.responseText +
                '&readyState=' + xhr.readyState)
            },
            statusCode: {
              // 
              200: function () {
                console.log('200: ')
              },
              404: function () {
                console.log('404: ')
                alert('404: ')
              },
              500: function () {
                console.log('500: ')
                alert('500: ')
              }
            }
          })
        }
      }

      // 
      // _this.$router.push('/index/' + postData.queryUrl)
    },
    // 
    renderQueryList () {
      let _this = this
      let result = _this.expertQueryList

      // loading
      $('div.load-container').css({'display': 'none'})

      // 
      let itemPerPage = 10

      let jqCurrentPage = $('#query-result-current-page')
      let jqTotalPages = $('#query-result-total-pages')
      let jqQueryResultPreviousPage = $('#query-result-previous-page')
      let jqQueryResultNextPage = $('#query-result-next-page')
      let jqQueryResult = $('article#query-result')

      jqCurrentPage.empty()
        .append(_this.expertQueryListPageCurrent)

      jqTotalPages.empty()
        .append(_this.expertQueryListPageTotal)

      // console.log(_this.expertQueryListPageCurrent)
      // console.log(_this.expertQueryListPageTotal)
      // console.log(jqQueryResultPreviousPage.hasClass('disabled'))
      // console.log(jqQueryResultNextPage.hasClass('disabled'))

      //  & 
      if (_this.expertQueryListPageTotal <= 1) {
        if (!jqQueryResultPreviousPage.hasClass('disabled')) {
          jqQueryResultPreviousPage.addClass('disabled')
        }
        if (!jqQueryResultNextPage.hasClass('disabled')) {
          jqQueryResultNextPage.addClass('disabled')
        }
      } else {
        // 
        if (_this.expertQueryListPageCurrent === 1) {
          // 
          if (!jqQueryResultPreviousPage.hasClass('disabled')) {
            jqQueryResultPreviousPage.addClass('disabled')
          }
          // 
          if (jqQueryResultNextPage.hasClass('disabled')) {
            jqQueryResultNextPage.removeClass('disabled')
          }
        }
        // 
        if (_this.expertQueryListPageCurrent === _this.expertQueryListPageTotal) {
          // 
          if (jqQueryResultPreviousPage.hasClass('disabled')) {
            jqQueryResultPreviousPage.removeClass('disabled')
          }
          // 
          if (!jqQueryResultNextPage.hasClass('disabled')) {
            jqQueryResultNextPage.addClass('disabled')
          }
        }
      }

      //  html_template 
      let htmlTemplate = '<p id="search-result-tag"> - </p>' +
        '<p id="search-result-info"> ' + _this.expertQueryListPageTotal +
        '  ' + _this.expertQueryListPageCurrent + ' ' +
        ' ' + (result.length - 1) + ' </p>'

      //  10 
      let i
      for (i = 0; i < itemPerPage && i < (result.length - 1); i++) {
        htmlTemplate += '<dl class="expert-profile">'
        let thisScore = parseFloat(result[i].score).toFixed(0)
        // URL
        if (!result[i].img_url || result[i].img_url === '' || result[i].img_url === null) {
          htmlTemplate +=
            '<dt class="dt-img"><img src="../../static/asset/image/default_expert_image.png" ' +
            'data-id="' + result[i].id + '" alt="researcher image" />' +
            '</dt>' + '<dd><p>' + result[i].name + '</p></dd>' +
            // 
            '<dd><p><strong>' + thisScore + '</strong></p></dd>' +
            '<dd><p><strong></strong>' + result[i].university + '</p></dd>' +
            '<dd><p><strong></strong>' + result[i].college + '</p></dd>' +
            '<dd><p><strong></strong>'
          if (!result[i].theme_list || result[i].theme_list === '' || result[i].theme_list === null) {
            htmlTemplate += '</p></dd>'
          } else {
            // html_template += result[i].theme_list.split("", 1)[0] + '</p>' +
            //  20 
            let maxLength = 20
            if (result[i].theme_list.length > maxLength) {
              htmlTemplate += result[i].theme_list.substring(0, maxLength) + '...</p></dd>'
            } else {
              htmlTemplate += result[i].theme_list + '</p></dd>'
            }
          }
        } else {
          htmlTemplate +=
            '<dt class="dt-img">' + '<img src="' + result[i].img_url + '" ' +
            'data-id="' + result[i].id + '" alt="researcher image" />' +
            '</dt>' + '<dd><p>' + result[i].name + '</p></dd>' +
            '<dd><p><strong>' + thisScore + '</strong></p></dd>' +
            '<dd><p><strong></strong>' + result[i].university + '</p></dd>' +
            '<dd><p><strong></strong>' + result[i].college + '</p></dd>' +
            '<dd><p><strong></strong>'
          if (!result[i].theme_list || result[i].theme_list === '' ||
            result[i].theme_list === null || result[i].theme_list === undefined) {
            htmlTemplate += '</p></dd>'
          } else {
            // html_template += result[i].theme_list.split("", 1)[0] + '</p>' +
            //  20 
            let maxLength = 20
            if (result[i].theme_list.length > maxLength) {
              htmlTemplate += result[i].theme_list.substring(0, maxLength) + '...</p></dd>'
            } else {
              htmlTemplate += result[i].theme_list + '</p></dd>'
            }
          }
        }
        htmlTemplate += '</dl>'

        // //  ExpertProfile 
        // htmlTemplate += '<expert-profile :result="expertQueryList[i]"></expert-profile>'
      }

      //  query-result-list 
      $('.query-result-list').empty()
      //  query-result-list 
        .append(htmlTemplate)

      // 
      $('div.query-result-list-div dt.dt-img img').unbind('click').click(function () {
        _this.jumpExpertDetailPage($(this).attr('data-id'))
      })

      //  article
      jqQueryResult.css({'display': 'block'})
    },
