    // 
    queryFunction () {
      'use strict'
      let _this = this
      let queryType = $('#query-type').val().trim()

      if (queryType === 'normal') {
        // 
        let jqInput = $("input[name='query-input']")
        let queryInput = jqInput.val().trim()
        let querySelection = $('#query-selection').val().trim()
        let queryIndustry = $('li#industry li.selected-industry > a').attr('industry-id') // .trim()

        if (!querySelection || querySelection === '') {
          alert('')
          return false
        } else {
          if (querySelection === 'industry') {
            // ""
            if (typeof queryIndustry === 'undefined') {
              alert('')
              return false
            }
          } else {
            if (!queryInput || queryInput === '') {
              jqInput.focus()
              return false
            }
          }
          // console.log('queryFunction() query-input=' + queryInput +
          //   '&query-type=' + queryType +
          //   '&query-selection=' + querySelection +
          //   '$queryIndustry=' + queryIndustry)

          let postData = {}
          postData.query_input = queryInput
          postData.query_type = queryType
          postData.query_selection = querySelection
          postData.query_industry = queryIndustry
          postData.researcher_input = ''
          postData.field_input = ''
          postData.research_content_input = ''
          postData.organization_input = ''
          postData.queryUrl = querySelection + '=' + queryInput

          this.queryCondition = postData
          this.queryListAjax(postData, 1)

          // 
          // _this.pushState(postData.queryUrl)
          _this.pushState('')
        }
      } else if (queryType === 'advanced') {
        // 
        let jqResearcherInput = $('input#researcher-input')
        let researcher = jqResearcherInput.val().trim()
        let field = $('#field-input').val().trim()
        // let researchContent = $('#research-content-input').val().trim()
        let organization = $('#organization-input').val().trim()
        let industry = $('select.industry-select option:selected').val()

        // console.log(
        //   'queryFunction() query-type=' + queryType +
        //   '&researcher-input=' + researcher +
        //   '&field-input=' + field +
        //   '&organization-input=' + organization +
        //   '&industry-input=' + industry)

        if ((!researcher && !field && !organization && !industry) ||
          (researcher === '' && field === '' && organization === '' && industry === '')) {
          jqResearcherInput.focus()
          return false
        } else {
          let postData = {}
          postData.query_input = ''
          postData.query_type = queryType
          postData.query_selection = ''
          postData.researcher_input = researcher
          postData.field_input = field
          postData.organization_input = organization
          postData.industry_input = industry
          postData.queryUrl = 'researcher=' + researcher + '&field=' + field +
            '&organization=' + organization + '&industry=' + industry

          this.queryCondition = postData
          this.queryListAjax(postData, 1)

          // 
          // _this.pushState(postData.queryUrl)
          _this.pushState('')
        }
      } else {
        return false
      }
    },
