    // -
    previousQueryResultPage () {
      'use strict'
      let _this = this

      let jqCurrentPage = $('#query-result-current-page')
      let jqQueryResultPreviousPage = $('#query-result-previous-page')
      let jqQueryResultNextPage = $('#query-result-next-page')

      if (!this.expertQueryListPageCurrent || this.expertQueryListPageCurrent <= 0 ||
        this.expertQueryListPageCurrent > this.expertQueryListPageTotal) {
        console.log('result-page-error! expertQueryListPageCurrent is out of range.')
        return false
      } else if (this.expertQueryListPageCurrent === 1) {
        alert('')
        return false
      } else {
        this.expertQueryListPageCurrent--

        jqCurrentPage.empty().append(this.expertQueryListPageCurrent)

        // ,
        if (this.expertQueryListPageCurrent === 1 && !jqQueryResultPreviousPage.hasClass('disabled')) {
          jqQueryResultPreviousPage.addClass('disabled')
        }

        // disabled,
        if (jqQueryResultNextPage.hasClass('disabled')) {
          jqQueryResultNextPage.removeClass('disabled')
        }

        // 
        _this.changeQueryResultPage(this.expertQueryListPageCurrent)
      }
      return true
    },
    // -
    nextQueryResultPage () {
      'use strict'
      let _this = this

      let jqCurrentPage = $('#query-result-current-page')
      let jqQueryResultPreviousPage = $('#query-result-previous-page')
      let jqQueryResultNextPage = $('#query-result-next-page')

      if (!this.expertQueryListPageCurrent || this.expertQueryListPageCurrent <= 0 ||
        this.expertQueryListPageCurrent > this.expertQueryListPageTotal) {
        console.log('next-page-error! expertQueryListPageCurrent is out of range.')
        return false
      } else if (this.expertQueryListPageCurrent === this.expertQueryListPageTotal) {
        alert('')
        return false
      } else {
        this.expertQueryListPageCurrent++

        jqCurrentPage.empty().append(this.expertQueryListPageCurrent)

        // ,
        if (this.expertQueryListPageCurrent === this.expertQueryListPageTotal &&
          !jqQueryResultNextPage.hasClass('disabled')) {
          jqQueryResultNextPage.addClass('disabled')
        }

        // disabled,
        if (jqQueryResultPreviousPage.hasClass('disabled')) {
          jqQueryResultPreviousPage.removeClass('disabled')
        }

        // 
        _this.changeQueryResultPage(this.expertQueryListPageCurrent)
      }
      return true
    },
    // -
    jumpQueryResultPage () {
      'use strict'
      let _this = this

      let jqCurrentPage = $('#query-result-current-page')
      let jqQueryResultPreviousPage = $('#query-result-previous-page')
      let jqQueryResultNextPage = $('#query-result-next-page')
      let jqQueryResultJumpInput = $('#query-result-jump-input')
      let pageInput = Math.floor(parseInt(jqQueryResultJumpInput.val().toString().trim(), 10))

      if (!this.expertQueryListPageCurrent || this.expertQueryListPageCurrent <= 0 ||
        this.expertQueryListPageCurrent > this.expertQueryListPageTotal) {
        console.log('input-jump-page-error! expertQueryListPageCurrent is out of range.')
        return false
      } else if (!pageInput || pageInput === '') {
        alert('')
        jqQueryResultJumpInput.focus()
        return false
      } else if (parseInt(pageInput, 10) <= 0 || parseInt(pageInput, 10) > this.expertQueryListPageTotal) {
        alert('' + this.expertQueryListPageTotal + '')
        jqQueryResultJumpInput.focus()
        return false
      } else {
        this.expertQueryListPageCurrent = parseInt(pageInput, 10)

        jqCurrentPage.empty().append(this.expertQueryListPageCurrent)

        // ,
        if (this.expertQueryListPageCurrent === 1 && !jqQueryResultPreviousPage.hasClass('disabled')) {
          jqQueryResultPreviousPage.addClass('disabled')
        }

        // ,
        if (this.expertQueryListPageCurrent === this.expertQueryListPageTotal &&
          !jqQueryResultNextPage.hasClass('disabled')) {
          jqQueryResultNextPage.addClass('disabled')
        }

        // ,
        if (this.expertQueryListPageCurrent > 1 && this.expertQueryListPageCurrent < this.expertQueryListPageTotal) {
          if (jqQueryResultPreviousPage.hasClass('disabled')) {
            jqQueryResultPreviousPage.removeClass('disabled')
          }
          if (jqQueryResultNextPage.hasClass('disabled')) {
            jqQueryResultNextPage.removeClass('disabled')
          }
        }

        // 
        _this.changeQueryResultPage(this.expertQueryListPageCurrent)
      }

      return true
    },
    // 
    changeQueryResultPage (currentPage) {
      'use strict'
      let _this = this

      _this.queryListAjax(_this.queryCondition, currentPage)

      // 
      // _this.pushState(_this.queryCondition.queryUrl)
      _this.pushState('')
    },
    // 
    initIndex () {
      // this
      let _this = this

      // inputenter
      $('#query-input').keydown(function (event) {
        // Enter
        if (event.keyCode === 13 || event.key === 'Enter') {
          _this.queryFunction()
        }
      })

      // ccn-selection-list 
      $('div.ccn-selection-list ul li').unbind('click').click(function () {
        let jqThis = this
        $(jqThis).addClass('active').siblings().removeClass('active')
        //  ccn-hottest-expert-content 
        let ccnExperts = _this.indexPageData.ccn_experts
        let showField = _this.ccnToList($(jqThis).attr('id'))
        let divIndex = _this.listToDiv(showField)

        _this.renderExperts('ccn-hottest-expert-' + divIndex.toString(), ccnExperts[showField].experts)
      }).mouseover(function () {
        $(this).css({'background-color': '#87cefa'})
      }).mouseout(function () {
        $(this).css({'background-color': '#fff'})
      })

      // query-selection-list 
      $('div.query-selection-list li.query-selection-choice').unbind('click').click(function () {
        let jqThis = this
        $(jqThis).addClass('active').siblings().removeClass('active')
        // 
        $('input#query-selection').val($(jqThis).attr('id'))
      }).mouseover(function () {
        $(this).css({'background-color': '#87cefa'})
      }).mouseout(function () {
        $(this).css({'background-color': '#ffffff'})
      })

      // hover: change background-color
      $('#query-button').mouseover(function () {
        $(this).css('background-color', '#6495ed')
      }).mouseout(function () {
        $(this).css('background-color', '#87cefa')
      })

      $('div.query-selection-list li#industry li').mouseover(function () {
        $(this).css({'background-color': '#87cefa'})
      }).mouseout(function () {
        $(this).css({'background-color': '#ffffff'})
      })

      $('div.query-selection-list li#industry li.industry-choice').unbind('click').click(function () {
        $('div.query-selection-list li#industry li').removeClass('selected-industry')
        $(this).addClass('selected-industry')
        _this.queryFunction()
      })

      $('#query-type-button').mouseover(function () {
        $(this).css({'background-color': '#6495ed'})
      }).mouseout(function () {
        $(this).css({'background-color': '#87cefa'})
      })

      // inputplaceholder
      $('input#query-input').focus(function () {
        $('input#query-input').attr({'placeholder': ''})
      }).blur(function () {
        $('input#query-input').attr({'placeholder': ''})
      })

      let jqueryWindow = $(window)
      let jqueryBody = $('body')
      let jqueryMenu = $('#menu')
      let jquerySidebar = $('#sidebar')
      let jqueryMain = $('#main')

      // Breakpoints.
      breakpoints({
        xlarge: ['1281px', '1680px'],
        large: ['981px', '1280px'],
        medium: ['737px', '980px'],
        small: ['481px', '736px'],
        xsmall: [null, '480px']
      })

      // Play initial animations on page load.
      jqueryWindow.on('load', function () {
        window.setTimeout(function () {
          jqueryBody.removeClass('is-preload')
        }, 100)
      })

      // Menu.
      jqueryBody.append(jqueryMenu)
      // jqueryMenu.panel({
      //   delay: 500,
      //   hideOnClick: true,
      //   hideOnSwipe: true,
      //   resetScroll: true,
      //   resetForms: true,
      //   side: 'right',
      //   target: jqueryBody,
      //   visibleClass: 'is-menu-visible'
      // })
      jqueryMenu.attr('jqueryMenu', 'is-menu-visible')

      // Search (header).
      let jquerySearch = $('#search')
      let jquerySearchInput = jquerySearch.find('input')

      jqueryBody
        .on('click', '[href="#search"]', function (event) {
          event.preventDefault()

          // Not visible?
          if (!jquerySearch.hasClass('visible')) {
            // Reset form.
            jquerySearch[0].reset()

            // Show.
            jquerySearch.addClass('visible')

            // Focus input.
            jquerySearchInput.focus()
          }
        })

      jquerySearchInput
        .on('keydown', function (event) {
          if (event.keyCode === 27) {
            jquerySearchInput.blur()
          }
        })
        .on('blur', function () {
          window.setTimeout(function () {
            jquerySearch.removeClass('visible')
          }, 100)
        })

      // Intro.
      let intro = $('#intro')

      // Move to main on <=large, back to sidebar on >large.
      breakpoints.on('<=large', function () {
        intro.prependTo(jqueryMain)
      })

      breakpoints.on('>large', function () {
        intro.prependTo(jquerySidebar)
      })

      //  session
      let jqUsername = $('input.username')
      let jqPassword = $('input.password')
      let jqLogin = $('section.menu a.login')
      let jqRegister = $('section.menu a.register')

      let serverUrl = 'check_login'
      let data = {}

      // HTTP Request (JQuery Ajax)
      $.ajax({
        type: 'GET',
        url: serverUrl,
        data: data,
        dataType: 'json',
        beforeSend: function (xhr) {
        },
        success: function (result, status, xhr) {
          console.log(result)
          if (!result || result === '' || result.length === 0) {
            alert('')
            console.log('HTTP result ')
            return false
          } else {
            // JSON
            let checkLoginResult = result.checkLoginResult

            // 
            console.log('checkLoginResult === ' + checkLoginResult)
            if (checkLoginResult === 'success') {
              console.log(' Session ')

              let userId = result.userId
              if (userId !== null && userId !== undefined) {
                //  userId  sessionStorage
                sessionStorage.setItem('user_id', userId)

                //  UI
                jqUsername.hide()
                jqPassword.hide()
                jqLogin.text('')

                // 
                if (!jqRegister.hasClass('disabled')) {
                  jqRegister.addClass('disabled')
                }

                //  VUEX 
                if (!_this.$store.state.loginFlag) {
                  _this.$store.dispatch('changeLoginFlag', true)
                }
              }
            } else if (checkLoginResult === 'fail') {
              console.log(' Session ')
            } else {
              console.log(' Session ')
            }
          }
        },
        error: function (xhr, status, error) {
          alert('HTTP GET Error! status=' + status + '&error=' + error +
            '&statusCode=' + xhr.status + '&responseText=' + xhr.responseText +
            '&readyState=' + xhr.readyState)
        },
        statusCode: {
          // 
          200: function () {
            console.log('200: ')
          },
          404: function () {
            console.log('404: ')
            alert('404: ')
          },
          500: function () {
            console.log('500: ')
            alert('500: ')
          }
        }
      })
    }
  }
}
</script>

